body {
  margin: 0;
  padding: 0;
  font-family: "Georgia", serif;
}
h1, h2, h3, h4, h5{
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  margin: 0;
}
.site-container{
  max-width: 1400px;
  margin: 0 auto;
}
p{
  margin: 0;
  font-family: "Georgia", serif;
  font-size: 20px
}
.flex-container{
  display: -ms-flexbox;
  display: flex;
  max-width: 1400px;
  margin: 0 auto;
}
.flex-one{
  -ms-flex: 1 1;
      flex: 1 1;
}
.flex-ten{
  -ms-flex: 10 1;
      flex: 10 1;
}
.flex-four{
  flex: 4;
}
.flex-six{
  flex: 6;
}
.flex-two{
  -ms-flex: 2 1;
      flex: 2 1;
}
a{
  text-decoration: none;
  color: black;
}
